@import url('https://fonts.googleapis.com/css?family=Roboto:400,700,900i&display=swap');

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Roboto";
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

h1, h2 {
  /* font-family: "Roboto Black Italic"; */
  font-style: italic;
  font-weight: 900;
  margin: 14px 0 14px 0;

}

h1 {
  font-size: 26px;
  text-align: center;
}
h2 {
  font-size: 18px;
}

p {
  line-height: 19px;
}

.orange-gradient {
  background-image: linear-gradient(to bottom, #EE4B75 , #FC625B);

}

.shadow {
  -webkit-box-shadow: 0px 10px 24px -11px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 10px 24px -11px rgba(0,0,0,0.75);
  box-shadow: 0px 10px 24px -11px rgba(0,0,0,0.75);
}

.input-field {
  border: 1px solid lightgray;
  background-color: #EEEEEE;
  width: 100%;
  height: 36px;
  margin-top: 3px;
}

.traffic-light {
  height: 18px;
}