.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  height: 60px;
  border-bottom: 0.5px solid grey;
  z-index: 100;
}
.toolbar__navigation {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
  padding: 0 1rem;
}

.toolbar__logo {
  position: absolute;
  left: 5%;

}

.toolbar__logo a {
  color: black;
  text-decoration: none;
  font-size: 1.5rem;
  font-family: 'Rancho', cursive;
  font-weight: bold;
}

.spacer {
  flex: 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #fa923f;
}

.toolbar__toggle-button {
  justify-self: flex-start;
}


@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}
